___CSS_LOADER_EXPORT___.push([module.id, `@media screen and (min-width: 320px) {
    .hero .img-wrapp {
        width: 100%;
        height: 620px;
        position: relative;
    }

    .hero .img-wrapp img {
        object-fit: cover;
        width: 100%;
        height: 620px;
        position: absolute;
        filter: brightness(0.7);
        top: 0;
        z-index: 0;
    }

    .hero .hero-text_wrapp {
        position: relative;
        height: 100%;
        display: flex;
        flex-direction: column;
        width: -webkit-fill-available;
        z-index: 5;
    }

    .hero .hero-text_wrapp .banner-text {
        margin-top: 20rem;
    }

    .hero .hero-text_wrapp .banner-text h2 {
        font-weight: bold;
        display: flex;
        flex-direction: column;
        line-height: 45px;
        font-size: 36px;
        text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.8);
        color: #DDDDDD;
        position: relative;
        z-index: 5;
    }

    .hero .hero-text_wrapp .banner-text h2 span.world-of-cuisine {
        font-size: 43px;
        font-weight: bold;
        text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.8);
        color: #F05454;
    }

    .hero .hero-text_wrapp .banner-text h2 span.a-story {
        font-size: 43px;
        font-style: italic;
        font-weight: bold;
        text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.8);
        color: #F56D15;
    }
}


@media screen and (min-width: 768px) {
    .hero .hero-text_wrapp .banner-text h2 {
        flex-direction: row;
        line-height: 50px;
        font-size: 40px;
    }

    .hero .hero-text_wrapp .banner-text h2 span.world-of-cuisine {
        font-size: 46px;
        padding-left: 5px;
    }

    .hero .hero-text_wrapp .banner-text h2 span.a-story {
        font-size: 44px;
        padding-left: 10px;
    }

}

@media (min-width: 1200px) {
    .hero .img-wrapp img {
      min-width: 1000px;
    }
  }`, "",{"version":3,"sources":["webpack://./src/scripts/component/hero/hero.css"],"names":[],"mappings":"AAAA;IACI;QACI,WAAW;QACX,aAAa;QACb,kBAAkB;IACtB;;IAEA;QACI,iBAAiB;QACjB,WAAW;QACX,aAAa;QACb,kBAAkB;QAClB,uBAAuB;QACvB,MAAM;QACN,UAAU;IACd;;IAEA;QACI,kBAAkB;QAClB,YAAY;QACZ,aAAa;QACb,sBAAsB;QACtB,6BAA6B;QAC7B,UAAU;IACd;;IAEA;QACI,iBAAiB;IACrB;;IAEA;QACI,iBAAiB;QACjB,aAAa;QACb,sBAAsB;QACtB,iBAAiB;QACjB,eAAe;QACf,2CAA2C;QAC3C,cAAc;QACd,kBAAkB;QAClB,UAAU;IACd;;IAEA;QACI,eAAe;QACf,iBAAiB;QACjB,2CAA2C;QAC3C,cAAc;IAClB;;IAEA;QACI,eAAe;QACf,kBAAkB;QAClB,iBAAiB;QACjB,2CAA2C;QAC3C,cAAc;IAClB;AACJ;;;AAGA;IACI;QACI,mBAAmB;QACnB,iBAAiB;QACjB,eAAe;IACnB;;IAEA;QACI,eAAe;QACf,iBAAiB;IACrB;;IAEA;QACI,eAAe;QACf,kBAAkB;IACtB;;AAEJ;;AAEA;IACI;MACE,iBAAiB;IACnB;EACF","sourcesContent":["@media screen and (min-width: 320px) {\n    .hero .img-wrapp {\n        width: 100%;\n        height: 620px;\n        position: relative;\n    }\n\n    .hero .img-wrapp img {\n        object-fit: cover;\n        width: 100%;\n        height: 620px;\n        position: absolute;\n        filter: brightness(0.7);\n        top: 0;\n        z-index: 0;\n    }\n\n    .hero .hero-text_wrapp {\n        position: relative;\n        height: 100%;\n        display: flex;\n        flex-direction: column;\n        width: -webkit-fill-available;\n        z-index: 5;\n    }\n\n    .hero .hero-text_wrapp .banner-text {\n        margin-top: 20rem;\n    }\n\n    .hero .hero-text_wrapp .banner-text h2 {\n        font-weight: bold;\n        display: flex;\n        flex-direction: column;\n        line-height: 45px;\n        font-size: 36px;\n        text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.8);\n        color: #DDDDDD;\n        position: relative;\n        z-index: 5;\n    }\n\n    .hero .hero-text_wrapp .banner-text h2 span.world-of-cuisine {\n        font-size: 43px;\n        font-weight: bold;\n        text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.8);\n        color: #F05454;\n    }\n\n    .hero .hero-text_wrapp .banner-te