___CSS_LOADER_EXPORT___.push([module.id, `@media screen and (min-width: 320px) {
  .app-bar {
      padding: 8px 16px;
      width: 100%;
      background-color: #F1EFEF;
      display: grid;
      grid-template-columns: auto 1fr auto;
      gap: 10px;
      position: sticky;
      top: 0;
      z-index: 99;
      box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2);
      position: fixed;
      width: -webkit-fill-available;
  }

  .app-bar_menu {
      display: flex;
      align-items: center;
  }

  .app-bar_menu button {
      background-color: transparent;
      border: none;
      font-size: 18px;
      padding: 8px;
      cursor: pointer;
      padding: 10px 15px;
      border: 1px solid #b0b0b0;
      border-radius: 5px;
      transition: all ease-out 0.1s;
  }

  .app-bar_menu button:focus {
      border: 1px solid #F15412;
      color: #F15412;
      transition: all ease-in 0.1s;
  }

  .app-bar_brands {
      display: flex;
      align-items: center;

  }

  .app-bar_brands .header_title {
    text-decoration: none;
  }

  .app-bar_brands h1 {
    line-height: 10px;
    font-size: 30px;
    color: #F56D15;
    user-select: none;
    font-weight: 500;
    padding: 10px;
  }

  .app-bar_brands h1 .second-title {
    font-size: 35px;
    font-weight: bold;
    color: #5E17EB;

    }

  .app-bar_navigation {
      position: absolute;
      top: 60px;
      left: -180px;
      width: 150px;
      transition: all 0.3s;
      padding: 8px;
      background-color: #F1EFEF;
      overflow: hidden;
      border-bottom-right-radius: 15px;
      filter: drop-shadow(0 3px 2px rgba(0, 0, 0, 0.50));
  }

  .app-bar_navigation.open {
      left: 2px;
  }

  .app-bar_navigation ul li a {
      display: inline-block;
      text-decoration: none;
      /* list-style: none; */
      padding: 10px;
      margin-bottom: 5px;
      width: 100%;
      font-size: 1.334rem;
      color: #34364a;
      font-weight: 500;
      min-height: 44px;
  }

  .app-bar_navigation ul li a.active {
      color: #F56D15;
  }
}

@media screen and (min-width: 768px) {
  .app-bar {
      grid-template-columns: 1fr auto;
      padding: 8px 32px;
  }

  .app-bar_brands h1 {
      font-size: 4em;
  }
  .app-bar_brands h1 .second-title {
    font-size: 48px;
}
  .app-bar_menu {
      display: none;
  }

  .app-bar_navigation {
      position: static;
      width: 100%;
      filter: none;
  }

  .app-bar_navigation ul li {
      display: inline-block;
  }

  .app-bar_navigation ul li a {
      display: inline-block;
      width: 120px;
      text-align: center;
      margin: 0;
  }
}

@media screen and (min-width: 1024px) {
  .app-bar {
      padding: 8px 83px;
  }
}
